    monospace;
}



.homepage-wrapper {
    color: white;
    display: flex;
    flex-direction: column;
    font-size: 1.5em;
    height: 75vh;
}

.intro-text {
    text-align: left;
    margin: 50px 100px 50px 100px;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

#playwright {
    -webkit-animation: fadein 2s;
            animation: fadein 2s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
}


#poet {
       -webkit-animation: fadein 2s;
               animation: fadein 2s;
       -webkit-animation-delay: 3s;
               animation-delay: 3s;
       -webkit-animation-fill-mode: backwards;
               animation-fill-mode: backwards;
}





#spy {
    
      -webkit-animation: fadein 2s;
    
              animation: fadein 2s;
      -webkit-animation-delay: 5s;
              animation-delay: 5s;
      -webkit-animation-fill-mode: backwards;
              animation-fill-mode: backwards;
}



#intro {

      -webkit-animation: fadein 2s;

              animation: fadein 2s;
      -webkit-animation-delay: 7s;
              animation-delay: 7s;
      -webkit-animation-fill-mode: backwards;
              animation-fill-mode: backwards;
}



